button {
    font-size: 18px;
    height: 50px;
}

.container{
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 auto;
    border: 1px solid gray;
    box-sizing: border-box;
}

.buttons_block{
    width: 100%;
    height: 50px;
    display: inline-flex;
    justify-content: space-around;
    margin-bottom: 20px;
    border: 1px solid darkslategray;
}

.error_block{
    font-size: 20px;
    span:not(:last-of-type){
        margin-right: 20px;
    }
    margin-bottom: 20px;
}
table {
    width: 100%;
    background: lightgray;
    color: white;
    border-spacing: 2px;
    font-size: 18px;
}
td, th {
    text-align: center;
    background-color: rgba(15,83,10,0.5);
}

.custom_block{
    width: auto;
    height: 100%;
    display: inline-flex;
    justify-content: left;
}